<template>
  <v-container
    fluid
    grid-list-md>
    <v-layout
      row
      wrap>
      <v-flex md12>
        <material-card
          color="primary"
          title="Pesquisar Plano de Pagamento">
          <v-card class="elevation-0">
            <v-card-text>
              <v-form>
                <v-layout
                  row
                  wrap>
                  <v-flex md3>
                    <v-text-field
                      v-model="filtros.sigla"
                      label="Sigla"
                    />
                  </v-flex>
                  <v-flex md3>
                    <v-text-field
                      v-model="filtros.descricao"
                      label="Descrição"
                    />
                  </v-flex>
                  <v-flex md3>
                    <v-text-field
                      v-model="filtros.qtdParcelas"
                      type="number"
                      min="0"
                      label="Quantidade Parcelas"
                      reverse
                    />
                  </v-flex>
                </v-layout>
              </v-form>
            </v-card-text>
            <v-card-actions>
              <v-spacer />
              <v-btn
                :loading="loadingBtn"
                color="primary"
                @click="pesquisar">Pesquisar</v-btn>
            </v-card-actions>
          </v-card>
        </material-card>
      </v-flex>
      <v-flex md12>
        <v-card>
          <v-toolbar
            flat
            color="white">
            <v-toolbar-title>Planos de Pagamento</v-toolbar-title>
            <v-divider
              class="mx-2"
              inset
              vertical
            />
            <v-spacer />
            <v-btn
              small
              fab
              color="primary">
              <v-icon @click="newItem()">mdi-plus</v-icon>
            </v-btn>
          </v-toolbar>
          <v-progress-linear
            :active="loading"
            color="light-blue"
            indeterminate
            height="3"
          />
          <v-data-table
            :headers="headers"
            :items="items"
            :total-items="totalItems"
            :pagination.sync="pagination"
            class="elevation-1 arrow-pagintion"
          >
            <template
              slot="headerCell"
              slot-scope="{ header }">
              <span
                class="subheading font-weight-light text--darken-3"
                v-text="header.text" />
            </template>
            <template
              slot="items"
              slot-scope="{ item }"
              ma-5
            >
              <td>{{ item.sigla }}</td>
              <td>{{ item.descricao }}</td>
              <td>{{ item.qtdParcelas }}</td>
              <td v-if="!!item.valorMinimoParcela">{{ item.valorMinimoParcela.toLocaleString("pt-BR", { minimumFractionDigits: 2 }) }}</td>
              <td v-else>{{ '-' }}</td>
              <td v-if="!!item.dataCadastro">{{ item.dataCadastro | moment("DD/MM/YYYY") }}</td>
              <td v-else>{{ '-' }}</td>
              <td v-if="!!item.dataExclusao">{{ item.dataExclusao | moment("DD/MM/YYYY") }}</td>
              <td v-else>{{ '-' }}</td>
              <td class="justify-end layout ma-2">
                <template v-if="!!item.dataExclusao">
                  <v-icon
                    class="mr-2"
                    title="Visualizar Plano"
                    @click="visualizarItem(item)"
                  >mdi-magnify</v-icon>
                </template>
                <template v-else>
                  <v-icon
                    class="mr-2"
                    color="blue"
                    title="Editar plano"
                    @click="editItem(item)"
                  >mdi-pencil</v-icon>
                  <v-icon
                    color="red"
                    title="Excluir plano"
                    @click="openDialogDelete(item)"
                  >mdi-delete</v-icon>
                </template>
              </td>
            </template>
          </v-data-table>
        </v-card>
      </v-flex>
      <template>
        <v-layout
          row
          justify-center>
          <v-dialog
            v-model="dialog"
            max-width="390">
            <v-card>
              <v-card-title class="headline">Excluir Plano de Pagamento</v-card-title>

              <v-card-text>Confirma a exclusão desse plano, essa operação não poderá ser desfeita?</v-card-text>

              <v-card-actions>
                <v-spacer />

                <v-btn
                  outline
                  style="color: black !important"
                  @click="closeDialogDelete">Cancelar</v-btn>

                <v-btn
                  color="red"
                  @click="deleteItem">Excluir</v-btn>
              </v-card-actions>
            </v-card>
          </v-dialog>
        </v-layout>
      </template>
    </v-layout>
  </v-container>
</template>

<script>
import { PlanoPagamentoBusiness } from '../../business'
import { ACAO_EDITAR, ACAO_VISUALIZAR } from '../../constants'

export default {
  metaInfo: {
    titleTemplate: '%s - Planos de Pagamento'
  },
  data () {
    return {
      dialog: false,
      item: null,
      loading: false,
      loadingBtn: false,
      totalItems: 0,
      pagination: {
        pageNo: 1,
        rowsPerPage: 25,
        sortBy: 'qtdParcelas',
        descending: true
      },
      selected: [],
      filtros: {
        descricao: '',
        sigla: '',
        qtdParcelas: null
      },
      headers: [
        {
          text: 'Sigla',
          value: 'sigla'
        },
        {
          text: 'Descrição',
          value: 'descricao'
        },
        {
          text: 'Max. Parcelas',
          value: 'qtdParcelas'
        },
        {
          text: 'Valor Mínimo Parcela',
          value: 'valorMinimoParcela'
        },
        {
          text: 'Cadastrado Em',
          value: 'dataCadastro'
        },
        {
          text: 'Cancelado Em',
          value: 'dataExclusao'
        },
        {
          sortable: false,
          text: ''
        }
      ],
      items: []
    }
  },
  watch: {
    pagination: {
      handler () {
        this.paginar()
      }
    }
  },
  methods: {
    reload () {
      this.paginar()
    },
    newItem () {
      this.$router.push('cad-planopagamento')
    },
    editItem (item) {
      this.$router.push(`cad-planopagamento/${item.id}/${ACAO_EDITAR}`)
    },
    visualizarItem (item) {
      this.$router.push(`cad-planopagamento/${item.id}/${ACAO_VISUALIZAR}`)
    },
    openDialogDelete (item) {
      this.item = item
      this.dialog = true
    },
    closeDialogDelete () {
      this.item = null
      this.dialog = false
    },
    deleteItem () {
      PlanoPagamentoBusiness.delete(this.item.id)
        .then(response => {
          this.$root.showSucesso(response.data)
          this.closeDialogDelete()
          this.reload()
        })
        .catch(error => {
          this.$root.showErro(error.data)
          this.closeDialogDelete()
        })
    },
    pesquisar () {
      this.loadingBtn = true
      this.paginar()
    },
    paginar () {
      this.loading = true
      PlanoPagamentoBusiness.pagination(this.pagination, this.filtros).then(response => {
        this.items = response.data.content
        this.totalItems = response.data.totalElements
      })
        .finally(() => {
          this.loadingBtn = false
          this.loading = false
        })
    }
  }
}
</script>
